.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #282c34;
  color: white;
  padding: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  max-width: 600px;
  width: 100%;
  padding: 20px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.button-container {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: #61dafb;
  color: #282c34;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #4fa8c7;
}

.todo-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.input-container {
  display: flex;
  gap: 10px;
  align-items: flex-start;
}

.todo-input {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
}

.file-input-container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.file-input-label {
  display: inline-block;
}

.image-input {
  display: none;
}

.file-input-button {
  display: inline-block;
  padding: 10px 15px;
  background-color: #61dafb;
  color: #282c34;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.file-input-button:hover {
  background-color: #4fa8c7;
}

.file-info {
  display: flex;
  align-items: center;
  gap: 5px;
  background-color: #3a3f4b;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 14px;
}

.file-name {
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.clear-file-button {
  background: none;
  border: none;
  color: #ff6b6b;
  cursor: pointer;
  padding: 0 5px;
  font-size: 16px;
  transition: color 0.3s;
}

.clear-file-button:hover {
  color: #ff5252;
}

.image-preview {
  width: 100%;
  max-height: 200px;
  overflow: hidden;
  border-radius: 5px;
  background-color: #3a3f4b;
  padding: 10px;
}

.image-preview img {
  max-width: 100%;
  max-height: 180px;
  object-fit: contain;
}

.add-button {
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  background-color: #61dafb;
  color: #282c34;
  cursor: pointer;
  transition: background-color 0.3s;
}

.add-button:hover {
  background-color: #4fa8c7;
}

.todo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.todo-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 10px;
  background-color: #3a3f4b;
  border-radius: 5px;
  margin-bottom: 10px;
}

.todo-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-top: 5px;
}

.todo-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.todo-image {
  max-width: 100%;
  max-height: 200px;
  object-fit: contain;
  border-radius: 5px;
}

.completed {
  text-decoration: line-through;
  opacity: 0.7;
}

.delete-button {
  padding: 5px 10px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  background-color: #ff6b6b;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.delete-button:hover {
  background-color: #ff5252;
}
